<template>
	<view class="baojie">
		<view class="swiperbox">
			<swiper class="swiper" :circular="true" :indicator-dots="true" :autoplay="true">
				<swiper-item>
					<image src="/static/img/img_baojie.png" alt=""></image>
				</swiper-item>
				<swiper-item>
					<image src="/static/img/img_baojie.png" alt=""></image>
				</swiper-item>
				<swiper-item>
					<image src="/static/img/img_baojie.png" alt=""></image>
				</swiper-item>
			</swiper>
		</view>
		<view class="typebox">
			<view class="typebox-price">
				<view class="typebox-price-top">
					<view class="typebox-price-top-left">
						￥<span>{{typelist[typeIndex].price}}</span>起
					</view>
					<view class="typebox-price-top-title fontColor-999">
						已服务<span>1234</span>
					</view>
				</view>
				<view class="typebox-price-down">
					<span>{{typelist[typeIndex].category_name}}</span>
				</view>
			</view>
			<view class="typebox-type">
				<view class="typebox-type-left fontColor-999">
					<span>服务类别</span>
				</view>
				<view class="typebox-type-right" v-if="typelist">
					<picker @change="typeChange" :value="typeIndex" :range="typelist" range-key="category_name">
						<view class="uni-input">
							<span v-if="typeIndex==null" class="font28 fontColor-ccc">请选择</span>
							<span v-else class="font28">{{typelist[typeIndex].category_name}}</span>
							<image class="icon22" src="/static/image/icon_gd.png" mode=""></image>
						</view>
					</picker>
				</view>
			</view>
		</view>
		<view class="introduce">
			<view class="introduce-title">
				<span>服务介绍</span>
			</view>
			<view class="introduce-text">
				<image src="/static/image/huang/mipmap-mdpi/introduce.png" mode="widthFix"></image>
			</view>
		</view>
		<!-- 立即预约 -->
		<a-order url='a_baojie_yuyue' type="家政保洁" :typeId='typelist[typeIndex].id'></a-order>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				// 保洁类别
				typelist: [{
					"category_name": "", //服务类型
					"id": 0, //编号
					"price": 0, //单价
					"service_introduction": ""//服务介绍
				}],
				typeIndex: 0
			}
		},

		onLoad() {
			this.getBaojieTypeList()
		},
		methods: {
			// 获取服务类型
			getBaojieTypeList() {
				this.util.ajax('personalDemand/housekeepingCategory', {
					"fromUserId": uni.getStorageSync('userId')
				}, res => {
					this.typelist = res.data.list
				})
			},
			// 监听服务类型
			typeChange(e){
				let that = this
				that.typeIndex = e.detail.value
			}
		},
	}
</script>

<style lang='less'>
	.baojie {
		background-color: #F6F6F6;

		.swiper {
			height: 503rpx;

			image {
				width: 100%;
				height: 100%;
			}
		}

		.typebox {
			margin: 16rpx 0;
			padding: 26rpx 46rpx;
			background-color: #fff;

			.typebox-price {
				.typebox-price-top {
					display: flex;
					justify-content: space-between;

					.typebox-price-top-left {
						color: #FF6000;

						span {
							font-size: 50rpx;
							margin: 0 20rpx;
						}
					}
				}

				.typebox-price-down {
					padding: 24rpx 0;
					margin-bottom: 24rpx;
					border-bottom: 2rpx solid #E5E5E5;

					span {
						font-size: 50rpx;
					}
				}
			}


			.typebox-type {
				display: flex;
				justify-content: space-between;

				.typebox-type-left {}

				.typebox-type-right {
					image {
						margin-left: 10rpx;
					}
				}
			}
		}

		.introduce {
			background-color: #fff;
			padding: 26rpx 46rpx;
			padding-bottom: 150rpx;

			.introduce-title {
				position: relative;

				span {
					font-size: 35rpx;
				}
			}

			.introduce-text {
				margin-top: 26rpx;
				text-align: center;
			}
		}
	}
</style>
